        "title": "The Eternal Dance",
        "body": "<p>As you step through the ring, every hair on your body stands as if electrified. Your feet begin to move in a complex pattern you were never taught, but now know in your blood.</p><p>You do not wish to ever stop dancing.</p><p><b>It is unlikely you ever will.</b></p>",
